export default {
	data() {
		return {
			title: 'Hello',
			list: [],
			pagination: 1,
			loadingText: '上拉加载更多',
			scrollHeight: 0
		}
	},
	onLoad() {
		let _this = this
		uni.getSystemInfo({
			success(res) {
				_this.scrollHeight = res.windowHeight
			}
		})
		this.clickdetail()
	},
	methods: {
		onPullDownRefresh() {
			this.pagination = 1;
			this.clickdetail();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		clickdetail: function(e) {
			uni.request({
				url: "https://m.jianzhimao.com/ajax/get-job-list?page=" + this.pagination +
					"&prov=&city=%E5%B9%BF%E5%B7%9E&area=&type=&time=",
				// url: "https://m.jianzhimao.com/job/pageDirect?page=" + this.pagination + "&city=%E5%B9%BF%E5%B7%9E", //仅为示例，并非真实接口地址。
				method: "GET",

				dataType: 'json',
				success: (res) => {
					this.list = res.data.data.list

				}
			});
		},
		lower: function(e) {
			this.pagination = this.pagination + 1

			this.loadingText = '加载中'
			uni.request({
				url: "https://m.jianzhimao.com/ajax/get-job-list?page=" + this.pagination +
					"&prov=&city=%E5%B9%BF%E5%B7%9E&area=&type=&time=",
				method: "GET",

				dataType: 'json',
				success: (res) => {

					if (res.data.data.length <= 0) {
						this.loadingText = '我是有底线的'
						return false
					}
					this.list = this.list.concat(res.data.data.list),
						this.loadingText = '上拉加载更多'

				}
			});

		},
		openinfo(e) {
			console.log(e)
			if(e==1){
				uni.navigateTo({
					url: '../common/common' ,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if(e==2){
				uni.navigateTo({
					url: '../zhizhao/zhizhao' ,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if(e==3){
				uni.navigateTo({
					url:'../miaorenwu/miaorenwu',
					success: res => {},
					fail: () => {},
					complete: () => {}
				})
			}
			
		}
	}
}
